body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  text-decoration: none;
  color: black;
  height: 100vh;
}

.text-align-center{
  text-align: center;
}

.padding-top-20px{
  padding-top: 20px;
}

.content {
  width: 100%;
  display: flex;
  flex-direction: row;
  margin-top: 18px;
}

.product-container {
  width: 100%;
  display: flex;
  flex-flow: wrap row;
  justify-content: center;
}

.left-content {
  width: 15%;
  border: 1px solid rgba(0,0,0,.125);
}

.middle-content {
  width: 60%;
}

.right-content {
  width: 25%;
  border: 1px solid rgba(0,0,0,.125);
  text-align: center;
}

.cart-img {
  width: 80px;
  height: 80px;
}

.product-image {
  width: 150px;
  height: 150px;
  align-self: center;
}

.product-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 10px 0px 10px 0;
}

/***fix card button ***/
.card-button {
  margin-bottom: 20px;
  width: 140px;
  align-self: center;
}

.card-container {
  margin: 5px 2px 5px 2px;
  width: 200px;
  height: 350px;
}
.card-container:hover {
  box-shadow: 2px 2px 2px 2px gray;
  opacity: 10;
  cursor: pointer;
}

.card-container .card {
  height: 100%;
}

.cart-container {
  margin: 24px;
  box-sizing: border-box;
  border: 1px solid;
  padding: 15px;
  display: flex;
  flex-flow: nowrap column;
  align-items: center;
}

/**for navbar link, color white**/
.navbar-dark .navbar-nav .nav-link a {
 color: white;
}

/*** cart-page **/
.cart-page-container{
  width: 100%; 
}

.cart-item-container{
  display: flex;
  justify-content: center;
}

.cart-item-image-wrapper{
  width: 100px;
  height: 120px;
}

.cart-item-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 10px 0px 10px 0;
}

.cart-item-details-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.cart-page-container hr {
  width: 50%; 
}

/*** cart-page **/
.side-menu-cart-page-container{
  width: 100%;
}

.side-menu-cart-item-container{
  display: flex;
  justify-content: center;
}

.side-menu-cart-item-image-wrapper{
  width: 60px;
  height: 80px;
}

.side-menu-cart-item-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 10px 0px 10px 0;
}

.side-menu-cart-item-details-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/***left side page- product search by type***/
.product-search-by-type-container {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  align-items: center;
}